import React, { useState, useEffect } from 'react';
import { Select } from 'antd';
import { collection, getDocs } from 'firebase/firestore';
import { IoIosSearch } from "react-icons/io";
import { db } from '../../config';
import { useLocation } from 'react-router-dom';

const { Option } = Select;

const SearchHome = () => {
    const [locations, setLocations] = useState([]);
    const [desiredAddressCode, setDesiredAddressCode] = useState('');
    const [selectedLocation, setSelectedLocation] = useState('all');
    const location = useLocation();

    useEffect(() => {
        const fetchLocations = async () => {
            try {
                const jobsCollection = collection(db, 'jobs');
                const jobsSnapshot = await getDocs(jobsCollection);
                const jobsData = jobsSnapshot.docs.map(doc => doc.data());
                const uniqueLocations = [...new Set(jobsData.flatMap(job => job.locations || []))];
                const parsedLocations = uniqueLocations.map((location, index) => {
                    if (location.includes('(')) {
                        const [mainName, districts] = location.split('(');
                        return {
                            code: index,
                            name: mainName.trim(),
                            districts: districts.slice(0, -1).split(', ')
                        };
                    }
                    return { code: index, name: location.trim() };
                });
                const uniqueParsedLocations = Array.from(
                    new Map(parsedLocations.map(location => [location.name.toLowerCase(), location])).values()
                );

                setLocations(uniqueParsedLocations);
            } catch (error) {
                console.error("Error fetching locations:", error);
            }
        };

        fetchLocations();

        const searchParams = new URLSearchParams(location.search);
        const query = searchParams.get('query') || '';
        const desiredAddressCode = searchParams.get('desiredAddressCode') || 'all';

        setDesiredAddressCode(query);
        setSelectedLocation(desiredAddressCode);
    }, [location]);

    const handleSearch = () => {
        const locationParam = selectedLocation === 'all' ? '' : `&desiredAddressCode=${selectedLocation}`;
        window.location.href = `/Careers/tim-kiem-viec-lam?query=${desiredAddressCode}${locationParam}`
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            handleSearch();
        }
    };

    return (
        <div className="search-container">
            <div className="search-box" data-cy="search-container">
                <div className="main-job__search-container">
                    <div className="main-job__search-wapper--backgound">
                        <div className="main-job__search-wapper">
                            <div className="main-job__search-input">
                                <div className="text-small">Bạn đang tìm kiếm</div>
                                <input
                                    placeholder="Việc làm, vị trí ứng tuyển..."
                                    value={desiredAddressCode}
                                    onChange={(e) => setDesiredAddressCode(e.target.value)}
                                    onKeyPress={handleKeyPress}
                                />
                            </div>
                            <div className="main-job__search-address" style={{ display: 'block' }}>
                                <div className="mr-2">
                                    <div className="text-small">Nơi bạn muốn làm việc</div>
                                    <Select
                                        value={selectedLocation}
                                        style={{ width: '100%' }}
                                        onChange={(value) => setSelectedLocation(value)}
                                        showSearch
                                        className="custom-selects"
                                    >
                                        <Option value="all">Tất cả</Option>
                                        {locations.map((location) => (
                                            <Option key={location.code} value={location.name}>{location.name}</Option>
                                        ))}
                                    </Select>
                                </div>
                            </div>
                            <div className="main-job__search-btn-box">
                                <div className="main-job__search-btn" style={{ background: '#ae8f3d', color: '#fff' }} onClick={handleSearch}>
                                    <span className="mr-2">
                                        <IoIosSearch size={20} />
                                    </span>
                                    <span>Tìm kiếm</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SearchHome;